export default {
    name: "subExperience",
  data(){
      return{
        checkList:[],
        api: this.GLOBAL.baseURL,
        audit_face:'',
        isAudit:false,
        active_id: ''
      }
  },
  methods:{
    change(){
      if(this.active_id){
          this.$http.post(this.api + '/ActivityE',{
            token: localStorage.getItem('token'),
            activity_id: this.active_id,
            visit:0
          })
            .then(res=>{
              if(res.data.code == 1){
                let a = res.data.data.step.replace(/[^0-9]/ig,' ').trim().split(/\s+/).length
                if(a ==15) {
                  if(this.audit_face == 2 || this.audit_face == 1){
                    this.$http.post(this.api + '/home/Activity/save_activity',{
                      token: localStorage.getItem('token'),
                      activity_id: this.active_id,
                      complete: 1,
                    })
                      .then(res=>{
                        if(res.data.code == 1){
                          this.$router.push('/publishList')
                        }else if(res.data.code == 3){
                          this.change()
                        }else if(res.data.code == 0){
                          alert(res.data.msg)
                        }
                      })
                  }else{
                   this.isAudit = true
                 }
                }else{
                  alert('您的体验还未填写完整！')
                }
              }else if(res.data.code == 3){
                this.change()
              }else if(res.data.code == 0){
                alert(res.data.msg)
              }
            })
      }
    },
    changeRouter(){
      this.$emit('changeRouter',{id:4,router:"Feel",information: this.active_id})
    },
    getUser(){
      this.$http.post(this.api + '/home/User/get_user',{
        token: localStorage.getItem('token')
      })
        .then(res=>{
          if(res.data.code == 1){
            this.audit_face =  res.data.data[0].audit_face
          }else if(res.data.code == 3){
            this.getUser()
          }else if(res.data.code == 0){
            alert(res.data.msg)
          }
        })
    },
    toAudit(){
      this.$router.push({
          path: '/authentication',
          query:{
            active_id: this.active_id
          }
        })
    }
  },
  mounted(){
      this.getUser()
  },
  created() {
      this.active_id = this.$route.query.information
  }
}



